<template>
  <layout-default>
    <div
      class="d-inline-block pa-l"
      v-for="metric in metrics"
      :key="metric"
    >
      <metric-gauge
        :metric="metric"
        class="pa-m"
      />
    </div>
  </layout-default>
</template>

<script>
import { config } from '../assets/globals'
import LayoutDefault from '../layouts/default'
import MetricGauge from '../components/common/energy/MetricGauge'
import { mapActions, mapMutations } from 'vuex'
export default {
  name: 'Home',
  components: {
    LayoutDefault,
    MetricGauge
  },
  computed: {
    metrics () {
      return config.metrics
    }
  },
  methods: {
    ...mapActions('meterFeed', ['fetchEnergyMeterFeed']),
    ...mapMutations(['setLoadingStatus'])
  },
  async created () {
    try {
      this.setLoadingStatus(true)
      await this.fetchEnergyMeterFeed()
      this.setLoadingStatus(false)
    } catch (error) {
      console.error('Could not fetch metrics', error)
    }
  }
}
</script>
