<template>
  <div class="container d-inline-block pa-m border--white border-box">
      <h3 class="mb-none text--white text-center">
        {{ metric }}
      </h3>
      <vue-speedometer
        :value="meterFeed[metric].value"
        :maxValue="meterFeed[metric].max"
        :minValue="meterFeed[metric].min"
        :maxSegmentLabels="0"
        :segments="30"
        :needleHeightRatio="0.8"
        startColor="#2E3440"
        endColor="#4C566A"
        needleColor="#D8DEE9"
        :needleTransitionDuration="4500"
        needleTransition="easeQuadInOut"
        currentValueText="${value} W"
        :height="200"
        textColor="#FFFFFF"
      />
      <div class="position-relative full-width border-box">
        <div class="min-max-caption">
          <strong>{{ meterFeed[metric].min }} W</strong>
          <strong>{{ meterFeed[metric].max }} W</strong>
        </div>
      </div>
    </div>
</template>

<script>
import { mapState } from 'vuex'
import VueSpeedometer from 'vue-speedometer'

export default {
  name: 'metricGauge',
  components: {
    VueSpeedometer
  },
  props: {
    metric: {
      type: String,
      required: true,
      default: 'metric property'
    }
  },
  computed: {
    ...mapState('meterFeed', ['meterFeed'])
  }
}
</script>

<style lang="scss">
.min-max-caption {
  display: flex;
  position: absolute;
  bottom: 27px;
  padding: 0 20px;
  justify-content: space-between;
  color: #fff;
  width: 100%;
  box-sizing: border-box;
}
</style>
